body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.row {
  margin-left: 40px;
  color: white;
}
.row h2 {
  padding-left: 20px;
  font-size: 1.4vw;
}
.row__posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px;
  padding-top: 10px !important;
}

.row__posters::-webkit-scrollbar {
  display: none;
}

.row__poster {
  width: 100%;
  object-fit: contain;
  max-height: 100px;
  margin-right: 10px;
  transition: -webkit-transform 450ms;
  transition: transform 450ms;
  transition: transform 450ms, -webkit-transform 450ms;
}

.row__poster:hover {
  -webkit-transform: scale(1.08);
          transform: scale(1.08);
}

.row__posterLarge {
  max-height: 250px !important;
}

.row__posterLarge:hover {
  -webkit-transform: scale(1.09);
          transform: scale(1.09);
}


.youtube__video{
  padding-left: 20px;
  padding-right: 60px;
}
@media screen and (max-width: 786px) {
  .row{
    margin-left: 20px;
  }
  .row h2{
    font-size: 1rem;
  }
}
.banner {
  color: white;
  object-fit: cover;
  height: 700px;
  z-index: -99;
  margin-bottom: -4rem;
  /* margin-bottom: 1rem; */
}
.banner__contents {
  margin-left: 60px;
  padding-top: 300px;
  height: 190px;
}

.banner__title {
  width: 600px;
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}

.banner__description {
  width: 45rem;
  line-height: 1.3;
  padding-top: 1rem;
  font-size: 0.9rem;
  max-width: 360px;
  height: 80px;
}
.banner__buttons {
  display: flex;
}
.banner__button i {
  font-size: 1.4rem;
  margin-right: 0.5rem;
}
.banner__button {
  display: flex;
  align-items: center;
  cursor: pointer;
  color: white;
  outline: none;
  border: none;
  font-size: 1rem;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 1.4rem;
  padding-right: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  margin-right: 1rem;
  margin-top: 1rem;
  color: black;
  background-color: white;
}

.banner__button:hover {
  color: #000;
  background-color: #cccaca;
  transition: all 0.2s;
}

.banner__button:nth-child(2) {
  padding-right: 1.5rem;
  color: white;
  background-color: rgba(255, 255, 255, 0.35);
}

.banner__button:nth-child(2):hover {
  background-color: rgba(255, 255, 255, 0.25);
  transition: all 0.2s;
}
.banner--fadeBottom {
  height: 480px !important;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
  );
}

@media screen and (max-width: 786px) {
  .banner__title {
    width: 50%;
    font-size: 2rem;
  }
  .banner__contents{
    margin-left: 35px;
  }
}

.nav {
  position: fixed;
  width: 100%;
  padding: 17px;
  padding-left: 60px;
  padding-right: 60px;
  top: 0;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  transition-timing-function: ease-in;
  transition: all .5s;
  background-image: linear-gradient(180deg , rgba(0,0,0,.8) , transparent);
}

.nav__black{
  background-color: #111;
}
.nav__logo {
  width: 92px;
  object-fit: contain;
}
.nav__avtar {
  width: 35px;
  border-radius: 5px;
  object-fit: contain;
}

*{
  margin: 0;padding: 0;box-sizing: border-box;
}
.App{
  background-color: #111;
}
